'use strict';

var Backbone = require('backbone');
var _ = require("lodash");

var $ = require("jquery");
var template = "<a href=\"#\"><div class=\"c-nav__item js-nav-item\">home</div></a>\n<a href=\"#shows\"><div class=\"c-nav__item js-nav-item\">shows</div></a>\n<a href=\"#schedule\"><div class=\"c-nav__item js-nav-item\">schedule</div></a>\n<a href=\"#wiki\"><div class=\"c-nav__item js-nav-item\">wiki</div></a>\n<a href=\"#projects\"><div class=\"c-nav__item js-nav-item\">projects</div></a>\n<a href=\"#about\"><div class=\"c-nav__item js-nav-item\">about</div></a>";

module.exports = Backbone.View.extend({
  render: function render() {
    this.$el.html(_.template(template));
    return this;
  },

  setActivePage: function setActivePage() {
    $(".js-nav-item").removeClass("c-nav__item--active").addClass("c-nav__item")
    var subURL = window.location.href.split("#")[1];
    if (subURL === undefined) {
        $('.js-nav-item').first().removeClass('c-nav__item').addClass('c-nav__item--active');
    } else {
      var url = "#" + subURL;
      var $navItem = $('.c-nav a[href="' + url + '"]');
      if ($navItem.length) {
        $navItem.children().removeClass('c-nav__item').addClass('c-nav__item--active');
      }
    }
  },
});